import React from "react";
import styles from "./index.module.scss";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { KeyboardArrowLeft, KeyboardArrowRight } from "@mui/icons-material";
const Pagination = ({ currentPage, totalPages, onPageChange }) => {
  const handlePrevPage = () => {
    if (currentPage > 1) {
      onPageChange(currentPage - 1);
    }
  };

  const handleNextPage = () => {
    if (currentPage < totalPages) {
      onPageChange(currentPage + 1);
    }
  };

  const renderPageNumbers = () => {
    const pageNumbers = [];
    const maxVisiblePages = 5;

    for (let i = 1; i <= totalPages; i++) {
      if (
        i === 1 ||
        i === totalPages ||
        Math.abs(i - currentPage) <= maxVisiblePages / 2
      ) {
        pageNumbers.push(i);
      } else if (pageNumbers[pageNumbers.length - 1] !== "...") {
        pageNumbers.push("...");
      }
    }

    return pageNumbers.map((number, index) => (
      <div
        key={index}
        className={`${styles.frame} ${
          number === currentPage ? styles.active : ""
        }`}
        onClick={() => {
          if (typeof number === "number") {
            onPageChange(number);
          }
        }}
      >
        <div className={styles.elementWrapper}>{number}</div>
      </div>
    ));
  };

  return (
    <div className={styles.pagination}>
      <div className={styles.frame} onClick={handlePrevPage}>
        <div className={styles.elementWrapperRightArrow}>
          <KeyboardArrowLeft color="#D2D3D4" />
        </div>
      </div>
      {renderPageNumbers()}
      <div className={styles.frame} onClick={handleNextPage}>
        <div className={styles.elementWrapperRightArrow}>
          <KeyboardArrowRight color="#D2D3D4" />
        </div>
      </div>
    </div>
  );
};

export default Pagination;
